body {
  background-color: #f4f4f4;
}

.search-form {
  background-color: #005085;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.search-form-input {
  border-color: rgba(0, 0, 0, 0);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 10px;
  display: flex;
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #505050;
  border-color: rgba(0, 0, 0, 0);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

img {
  max-width: 100%;
  object-fit: cover;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 100px;
  margin-bottom: 100px;
  display: flex;
}

.photo-card {
  max-width: 350px;
}

.info {
  background-color: #fff;
  border-radius: 0 0 4px 4px;
  justify-content: space-around;
  display: flex;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

